.moovaz-container .title {
  font-weight: 800;
  font-size: 28px;
  color: #2C3652;
  text-align: left; }

.moovaz-container .desc {
  font-weight: 600;
  font-size: 14px;
  color: #6E7F95; }

.moovaz-container .blue {
  font-weight: bold;
  font-size: 18px;
  color: #6480EB; }

.moovaz-container .icon-text {
  font-weight: bold;
  font-size: 12px;
  text-align: center;
  color: #2C3652; }

.moovaz-container .checkbox-input-desc {
  font-weight: normal;
  font-size: 14px;
  color: #6E7F95; }

.moovaz-container .main-img img {
  width: 100%;
  height: auto; }

.moovaz-container .moovaz-logo {
  float: right; }
  .moovaz-container .moovaz-logo img {
    margin-left: 10px;
    max-width: 182px;
    height: auto; }
  @media screen and (max-width: 991px) {
    .moovaz-container .moovaz-logo {
      margin-bottom: 30px; } }

.moovaz-container .sub-img-container {
  display: flex;
  flex-direction: column;
  align-items: center; }
  @media screen and (max-width: 767px) {
    .moovaz-container .sub-img-container {
      margin-bottom: 30px; }
      .moovaz-container .sub-img-container img {
        max-width: 150px;
        height: auto; } }

.moovaz-container .icon-container {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .moovaz-container .icon-container button {
    transition: transform 0.3s ease; }
    .moovaz-container .icon-container button:hover {
      transform: scale(1.1); }
  .moovaz-container .icon-container .icon-text {
    white-space: pre-wrap; }
